// bibliotek
import { useState } from 'react';  
import { motion } from 'framer-motion';
// components
import Paragraph from "../paragraph/Paragraph";
import useMousePosition from "../../hooks/useMousePosition.js"
// style
import "./about.scss"


export default function About(){

    const [isHovered, setIsHovered] = useState(false);
    const { x, y } = useMousePosition();
    const size = isHovered ? 400 : 40;

    return(
        <div className="about__wrapper">
            <Paragraph title="About us" />
            <div className="about__container">
                <motion.div 
                    className="about__mask"
                    animate={{
                    WebkitMaskPosition: `${x - (size/2)}px ${y - (size/2)}px`,
                    WebkitMaskSize: `${size}px`,
                    }}
                    transition={{ type: "tween", ease: "backOut", duration:0.5}}
                    >
                    <p onMouseEnter={() => {setIsHovered(true)}} onMouseLeave={() => {setIsHovered(false)}}>
                    Мы не копируем чужие идей, мы генерируем новые, чтобы создать уникальный дизайн, который отражает индивидуальность и стиль наших клиентов! 
                    </p>

                </motion.div>
                <div className='about__body'>
                    <p>Команда <span>молодых, амбициозных и творческих</span> профессионалов! Фантазёрки и мечтательницы, что не мешает нам ответственно подходить к организации своего дела. </p>
                </div>
            </div>
        </div>
    )
}
