// bibliotek
import React from "react";
import Marquee from "react-fast-marquee";
// style
import "./awards.scss";

function Awards(){
    return(
       <div className="awards__wrapper">
        <h6>Awards</h6>
            <div className="awards__container">
                <Marquee 
                    direction="right"
                    speed={150}
                    align="center"
                >
                        <div className="awards__item">
                            <img src={awards__item_1} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_2} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_3} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_4} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_5} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_6} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_7} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_8} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_9} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_10} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_11} alt="" />
                        </div>                        
                </Marquee>
            </div>
       </div>
    )
}
export default Awards;