// bibliotek
import React, { useState } from "react";
import { Link } from "react-router-dom";

import MagneticSocial from "../magneticSocial/MagneticSocial.js";

// style
import './header.scss';
// images
import homeHeader from "../../assets/header/home.svg";
// import telegram from "../../assets/header/telegram.svg"
// import whatsapp from "../../assets/header/whatsapp.svg"
// import linkedin from "../../assets/header/linkedin.svg"

import telega from "../../assets/icon/telegram.svg";
import whatsapp from "../../assets/icon/whatsapp.svg";

function Header() {
  
  const [isShown, setIsShown] = useState(false);

  const handleClick = event => {
    setIsShown(current => !current);
  };
  
    return (
      <>
      <HeaderNav shown={isShown} setshown={setIsShown}/>
      <header className="header">
        <Link to="/"><h2 className="logo">Q2<MagneticSocial><span>Studio</span></MagneticSocial></h2></Link>
        <button className="burger" onClick={handleClick}>
          {isShown && <HeaderNav />}
          <MagneticSocial><span className='burger__item_1'></span></MagneticSocial>
          <MagneticSocial><span className='burger__item_2'></span></MagneticSocial>
          <MagneticSocial><span className='burger__item_3'></span></MagneticSocial>
        </button>
        <div className="header__social">
          <MagneticSocial>
            <a href="https://t.me/Galchik" rel="noreferrer" target="_blank"><img  src={telega} alt="" /></a>
          </MagneticSocial>
          <MagneticSocial>
          <a href="https://wa.me/79163131255" rel="noreferrer" target="_blank"><img  src={whatsapp} alt="" /></a>
          </MagneticSocial>
          {/* <MagneticSocial>
            <a href="https://wa.me/79151462545" rel="noreferrer" target="_blank"><img  src={linkedin} alt="" /></a>
          </MagneticSocial> */}
        </div>
      </header>
      </>
    );
  }
  
  export default Header;

  function HeaderNav({shown}){
    return(
      <nav className={shown ? "nav__wrapper active" : "nav__wrapper"}>
          <ul >
            <li><Link className='nav_item' to="/"><img src={homeHeader} alt="" /><h5>Home</h5></Link></li>
            <li><Link to="/picture">Gallery</Link></li>
            <li><Link to="/services">Services</Link></li>
            <li><Link >Team</Link></li>
            <li><Link >Contact</Link></li>
          </ul>
      </nav>
    )
  }