import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, A11y, Autoplay } from 'swiper/modules';

import "./freight.scss";

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import Paragraph from '../paragraph/Paragraph';




export default function Freight({ freightObjDat }){
    return(
        <div className="freight__wrapper">
            <Paragraph title="Design trends"/>
            <Swiper
        // install Swiper modules
        // для slidesPerView={"auto"} необходимо прописать свойство .swiper-slide {width: auto!important;}
        modules={[Navigation, A11y, Autoplay]}
        spaceBetween={0}
        slidesPerView={"1"}
        autoplay={{
            delay: 8000,
            disableOnInteraction: false,
            waitForTransition: true,
          }}
        loop={true}
        speed={1300}
      >
        {freightObjDat?.map((item) => (
          <SwiperSlide key={item.id}>
            <div className="freight__container">
            <div className="freight__left">
                
                <div className="freight__left__title">
                    <div className="freight__left__title__img1"><img src={item.slide1_item_1} alt="" /><h5 style={{color: `${item.collorText1}`}}>{item.title_slide1_item_1}<h3>{item.subtitle_slide1_item_1}</h3></h5></div>
                    <div className="freight__left__title__img2"><img src={item.slide1_item_2} alt="" /><h5 style={{color: `${item.collorText2}`}}>{item.title_slide1_item_2}<h3>{item.subtitle_slide1_item_2}</h3></h5></div>
                </div>
                <div className="freight__left__center">
                    <div className="freight__left__center__img1"><img src={item.slide1_item_3} alt="" /></div>
                    <div className="freight__left__center__img2"><img src={item.slide1_item_4} alt="" /></div>
                </div>
            </div>
            <div className="freight__right" style={{backgroundColor: `${item.collorBg_1}`}}>
                <span style={{backgroundColor: `${item.collorBg_2}`}}></span>
                <div className="freight__right__top">
                    <div className="freight__right__top__img1"><img src={item.slide1_item_5} alt="" /></div>
                    <div className="freight__right__top__img2"><img src={item.slide1_item_6} alt="" /></div>
                </div>
                <div className="freight__right__bottom">
                    <h3 className="freight__right__bottom__visible" style={{color: `${item.collorText1}`}}>{item.title_slide1_item_2}</h3>
                    <h3 className="freight__right__bottom__invisible" style={{color: `${item.collorText2}`}}>{item.title_slide1_item_1}</h3>
                    <div className="freight__right__bottom__img1"><img src={item.slide1_item_7} alt="" /></div>
                    <div className="freight__right__bottom__img2"><img src={item.slide1_item_8} alt="" /></div>
                </div>
            </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
            
        </div>
    )
}