
import React, { useState } from "react";
import { useEffect } from "react";

// bibliotek
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";



// conponets
import CustomCursor from "../../components/CustomCursor";
import Header from "../../components/header/header";
import Main from "../../components/main/Main";
import Portfolio from "../../components/portfolio/Portfolio";
// import Gallery from "../../components/gallery/Gallery";
import Footer from "../../components/footer/Footer";
import Awards from "../../components/awards/Awards";
import About from "../../components/about/about";
import GridAnimation from "../../components/grid_animation/GridAnimation";
import Work from "../../components/work/Work";
import ScrollGorizont from "../../components/scrollGorizont/ScrollGorizont.jsx";

// база данных
import freightObjDat from "../../assets/freightObj.js";
// style
import "./home.scss";
import Freight from "../../components/freight/Freight.jsx";


gsap.registerPlugin(ScrollTrigger);



const Home = ()=>{

  // Прелоудер
  const [preloader, setPreloader] = useState(true);
    useEffect(()=>{
      const fakeDataFetch = () =>{
        setTimeout(() =>{
          setPreloader(false);
        }, 400);
      };
      fakeDataFetch();
    }, []);
    // 

    return(
        <>
          
        {preloader ? ( 
        <div className="loader__wrapper absolute">
            <h2>Q2 studio</h2>
            <h4>Дизайн интерьера</h4>
        </div> ):(
            <div className="main__container">
              <CustomCursor/>
            <section className="scroll__container"><Header/></section>
            <section className="scroll__container"><Main/></section>
            
            
            
            <section className="scroll__container"><Portfolio/></section>
            <section className="scroll__container"><Work/></section>
            <section className="scroll__container">
              <section className="scrollGorizont__container"><ScrollGorizont/></section>
            </section>
            {/* <section className="scroll__container"><Gallery/></section> */}
            
            <section className="scroll__container"><About/></section>
            <section className="scroll__container"><GridAnimation/></section>
            
            <section className="scroll__container"><Freight freightObjDat={freightObjDat}/></section>

            <section className="scroll__container"><Awards/></section>
            <section className="scroll__container"><Footer/></section>
            </div>
        
)}      
        </>
    )
}
export default Home;