// bibliotek
import React, {useState} from "react";
import Marquee from "react-fast-marquee";
import axios from "axios";
// style
import "./footer.scss"



// images
import footer_bg from "../../assets/footer/1.png"
import socialIcon_1 from "../../assets/icon/behance.svg";
import socialIcon_2 from "../../assets/icon/facebook.svg";
import socialIcon_3 from "../../assets/icon/gmail.svg";
import socialIcon_4 from "../../assets/icon/instagram.svg";
import socialIcon_5 from "../../assets/icon/pinterest.svg";
import socialIcon_6 from "../../assets/icon/vkontakte.svg";



function Footer(){

    const [messageName, setMessageName] = useState("");
    const [messageAdress, setMessageAdress] = useState("");
  
   const urlSend = `Заявка с сайта! \nИмя: ${messageName},\nАдрес: ${messageAdress}`
   
    const handleSubmit = async (e) => {
      e.preventDefault();
      try {
        await axios.post("6451764671:AAGizWg_3drCik5FseZI7JG4mCaECV-fRsA", {
          chat_id: "-1001867028495",
          text: urlSend,
      });
        alert("Уведомление успешно отправлено!");
        setMessageName("");
      } catch (error) {
        console.error(error);
        alert("Ошибка при отправке уведомления!");
      }
    };

    return(
        <div className="footer">
            <div className="footer__wrapper">
                <div className="footer__container">
                    <div className="footer__logo">
                        <Marquee speed={90}>
                            <h4>contacts</h4>
                            <h5>g</h5>
                            <h4>contacts</h4>
                            <h5>g</h5>
                        </Marquee>
                    </div>
                    <div className="footer__contact">
                    <form onSubmit={handleSubmit}>
                        <div className="segment">
                            <h2>Application</h2>
                        </div>
                        <label>
                            <input type="text" placeholder="Name" name="name" value={messageName} onChange={(e) => setMessageName(e.target.value)}/>
                        </label>
                        <label>
                            <input type="text" name="email" placeholder="Email Address" value={messageAdress} onChange={(e) => setMessageAdress(e.target.value)}  />
                        </label>
                        
                        <button className="red" type="submit">Let’s create"</button>
                    </form>
                    <div className="footer__connect">
                        <ul>
                            <div className="footer__connect__left">
                            <li className="footer__connect__item"><img src={socialIcon_2} alt="Facebook" /><a rel="noreferrer" target="_blank" href="https://github.com">Facebook<span>Только завели</span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_6} alt="Vkontakte" /><a rel="noreferrer" target="_blank" href="https://vk.com/id679210542">Vkontakte<span>Зайди узнаешь</span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_3} alt="Email" /><a rel="noreferrer" target="_blank" href="q_2@bk.ru">Email<span>Ответим и тут</span></a></li>
                            </div>
                            <div className="footer__connect__right">
                            <li className="footer__connect__item"><img src={socialIcon_4} alt="Instagram" /><a rel="noreferrer" target="_blank" href="https://www.instagram.com/q2s.tudio?igsh=czc1MnB0Y3Z2aW10">Instagram<span>Не Tik tok</span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_5} alt="Pinterest" /><a rel="noreferrer" target="_blank" href="https://pin.it/1KADo87mE">Pinterest<span>Наши задумки</span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_1} alt="Behance" /><a rel="noreferrer" target="_blank" href="https://www.behance.net/q_2?tpclid=facebook.PAAabnnKYPq8V7GQvyULusssN8sfGR9JAxJWgmzaj7RLMEhO6snXHK7sFnhN4_aem_AT4AfjtdP2Hn8Ror05wCmx6rdNM2Xz17RZ_1GHkeQEV-TiJ8_uGmGUoINJ_YIohscb0">Behance<span>Посмотри и узнаешь</span></a></li>
                            </div>
                        </ul>
                    </div>
                    </div>
                </div>
                <div className="footer__images">
                    <img src={footer_bg} alt="" />
                </div>
            </div>
            
        </div>
    )
}

export default Footer;